var self;
export default{
	onLoad(e) {
		self=this;
		console.log(e)
		self.changed=e.changed;
		console.log(self.changed)
	},
	data(){
		return{
			changed:0,
			pic_index:0,
			pic:[],
			preview:[],
			del:[],
			wh:"",
			takenotes:[1],
			note:1,
			piclist:[
				"../../../static/1.jpeg",
				"../../../static/126.jpg",
				"../../../static/131.jpg",
				"../../../static/153.jpg",
				"../../../static/166.jpg"
				]
		}
	},
	methods:{
		addPic:()=>{
			uni.showActionSheet({
			    itemList: ['图片', '视频'],
			    success: function (res) {
			        if(res.tapIndex==0){
						uni.chooseImage({
							count:5,
							sizeType: ['original', 'compressed'], 
							success: function (res) {
								res.tempFilePaths.forEach(function(e){
									if(self.pic_index>4){
										console.log("aa")
										uni.showToast({
											title:"最多选五张",
											icon:"none"
										})
										return false;
									}
									self.pic[self.pic_index]=e;
									self.pic_index++;
								})
								console.log(self.pic_index);
								console.log(self.pic);
							    }
						})
					}
					if(res.tapIndex==1){
						uni.chooseVideo({
							success:function(res){
								self.pic[self.pic_index]=res;
								self.pic_index++;
							}
						})
					}
			    }
			});
			
			
		},
		delPic:(index)=>{
			let j=self.pic_index-index;
			for(var i=0; i<j;i++){
				self.pic[index+i]=self.pic[index+i+1];
				self.pic.splice(index+i+1,0)
			}
			self.pic.pop();
			self.pic_index--;
		},
		previewPic:(index)=>{
			self.preview[0]=self.pic[index];
			uni.previewImage({
				urls:self.preview
			})
		},
		previewImg:(index)=>{
			self.preview[0]=self.piclist[index]
			uni.previewImage({
				urls:self.preview
			})
		},
		changeState:()=>{
			self.changed=1;
			uni.$emit('state',{changed:1});
			uni.navigateBack({})


		},
		aaa:()=>{
			console.log(self.changed)
		}
		}
}
